<template>
  <section
      :class="scrollPositionComputed ? 'sticky top-0 left-0 right-0 bg-green-200 z-50' : 'bg-gradient-to-b from-green-300 via-green-200 to-green-50'"
       style="transition: all 0.15s ease 0s;">
    <div
        class="px-6 lg:px-12"
        :class="scrollPositionComputed ? 'pt-0' : 'pt-6'"
        style="transition: all 0.15s ease 0s;">
      <nav class="flex justify-between my-0">
        <div class="flex w-full items-center">
          <div>
            <mylogo></mylogo>
          </div>
          <ul class="hidden xl:flex px-4 ml-14 2xl:ml-40 mr-auto items-end">
            <li
                v-for="(item, index) in navmenu"
                :key="'menu_' + index"
                class="mr-8 2xl:mr-14">
              <router-link
                  :to="{ name: item.name }"
                  class="flex items-center font-heading pb-2 px-4"
                  :class="currentPage(item, 'desktop')"
                  href="#">
                <svgicon :icon="item.icon" class="mr-2" :class="item.name === $route.name ? 'w-10 h-10' : 'w-5 h-5'"></svgicon>
                <span>{{$t('menu.' + item.name)}}</span>
              </router-link>
            </li>
            <!-- <svg
              width="8"
              height="5"
              viewbox="0 0 8 5"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                d="M6.97291 0.193232C7.20854 -0.0644107 7.58938 -0.0644107 7.82328 0.193232C8.05804 0.450875 8.05978 0.867141 7.82328 1.12478L4.42529 4.80677C4.19053 5.06441 3.81056 5.06441 3.57406 4.80677L0.176073 1.12478C-0.0586909 0.868102 -0.0586909 0.450875 0.176073 0.193232C0.411706 -0.0644107 0.792544 -0.0644107 1.02644 0.193232L4.00098 3.21284L6.97291 0.193232Z"
                fill="#8C949F"
              ></path>
            </svg> -->
          </ul>
          <div class="hidden xl:flex items-center">
            <div class="flex-shrink-0 w-px h-12 bg-gray-100 ml-9 mr-10"></div>
            <div class="flex items-center text-darkBlueGray-400 hover:text-darkBlueGray-500">
              <div class="mr-6 text-right">
                <span class="block font-heading font-medium leading-tight">
                  {{$t('profile.name')}}
                </span>
                <span class="font-heading font-medium text-xs text-darkBlueGray-300 uppercase leading-tight">
                  {{$t('profile.profession')}}
                </span>
              </div>
              <img
                class="mr-4 w-10 h-10 rounded-full object-cover"
                :src="userimage"
                alt=""
              />
            </div>
            <LocaleButton />
          </div>
        </div>
        <button @click="boolMobileMenu = !boolMobileMenu" class="navbar-burger self-center xl:hidden">
          <svgicon icon="burgermenu"></svgicon>
        </button>
      </nav>
    </div>
    <div v-if="boolMobileMenu" class="navbar-menu block xl:hidden fixed top-0 left-0 bottom-0 w-full z-50">
      <div @click="boolMobileMenu = false" class="navbar-backdrop fixed inset-0 bg-gray-800 opacity-80"></div>
      <nav class="relative flex flex-col py-20 px-10 h-full w-3/4 bg-white overflow-y-auto">
        <button @click="boolMobileMenu = false" class="navbar-close absolute top-10 right-10">
          <svgicon icon="x"></svgicon>
        </button>
        <div class="inline-block pb-4 mb-4 border-b border-blueGray-200" href="#">
          <mylogo></mylogo>
        </div>
        <div class="mb-8 flex justify-center">
          <clock></clock>
        </div>
        <ul class="mb-16">
          <li
              v-for="(item, index) in navmenu"
              :key="'menu_' + index"
              class="mr-8 2xl:mr-14">
            <router-link
                :to="{ name: item.name }"
                class="flex items-center pl-10 pr-24 py-4 bg-white rounded-3xl font-medium font-heading"
                :class="currentPage(item, 'mobile')"
                href="#">
              <svgicon :icon="item.icon" class="mr-2"></svgicon>
              <span>{{$t('menu.' + item.name)}}</span>
            </router-link>
          </li>
        </ul>
        <div class="mt-10">
          <LocaleButton />
        </div>
      </nav>
    </div>
  </section>
</template>
<script>
import svgicon from '@/components/svg.vue'
import mylogo from '@/components/logo.vue'
import clock from '@/components/clock.vue'
import LocaleButton from "@/components/LocaleButton"
import routes from '@/router/routes.js'
export default {
  components: {
    mylogo,
    svgicon,
    clock,
    LocaleButton
  },
  computed: {
    userimage () {
      return '/image/userimage.jpg'
    },
    navmenu () {
      return routes.filter(k => k.name === 'main')[0].children
    },
    scrollPositionComputed () {
      return this.scrollPosition > 20
    }
  },
  methods: {
    currentPage (item, type) {
      let borderstyle = ''
      let fontsize = ''
      if (type === 'desktop') {
        borderstyle = 'border-b-4 border-b-green-500 hover:border-b-green-700 rounded-3xl'
        fontsize = item.name === this.$route.name ? 'text-3xl font-bold' : 'text-base font-medium'
      } else if (type === 'mobile') {
        borderstyle = ''
      }
      return (item.name === this.$route.name ?
            'text-green-600 hover:text-green-800 ' + borderstyle :
            'text-black hover:text-green-600') + ' ' + fontsize
    },
    updateScroll() {
       this.scrollPosition = window.scrollY
    }
  },
  mounted() {
    window.addEventListener('scroll', this.updateScroll);
  },
  data () {
    return {
      boolMobileMenu: false,
      scrollPosition: 0
    }
  }
};
</script>
