<template>
  <div class="block xl:hidden flex justify-center">
    <span
        v-for="locale in $i18n.availableLocales"
        :key="`locale-${locale}`"
        @click="setLocale(locale)"
        class="rounded-full cursor-pointer"
        :class="[
          $i18n.locale === locale ?
              'text-' + config.colorName + '-600 hover:text-' + config.colorName + '-800 border-2 border-' + config.colorName + '-500 hover:border-' + config.colorName + '-700' :
              'text-black hover:text-' + config.colorName + '-600',
          'px-4 py-2 text-sm',
        ]">
      {{ $t('lang.' + locale) }}
    </span>
  </div>
  <Menu as="div" class="relative hidden xl:inline-block w-28 text-left z-50">
    <div>
      <MenuButton
        class="inline-flex justify-between w-full rounded-md px-4 py-2 bg-white text-sm font-medium text-gray-700 hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2"
        :class="'focus:ring-' + config.colorName + '-500 focus:ring-offset-' + config.colorName + '-100'">
        {{ $t('lang.' + $i18n.locale) }}
        <svgicon icon="cdown"></svgicon>
      </MenuButton>
    </div>

    <transition
      enter-active-class="transition ease-out duration-100"
      enter-from-class="transform opacity-0 scale-95"
      enter-to-class="transform opacity-100 scale-100"
      leave-active-class="transition ease-in duration-75"
      leave-from-class="transform opacity-100 scale-100"
      leave-to-class="transform opacity-0 scale-95"
    >
      <MenuItems
        class="origin-top-right absolute right-0 mt-2 w-full rounded-md shadow-lg bg-white ring-1 ring-opacity-5 focus:outline-none"
        :class="'ring-' + config.colorName + '-500'">
        <div class="py-1 text-right">
          <MenuItem
            v-slot="{ active }"
            v-for="locale in $i18n.availableLocales"
            :key="`locale-${locale}`"
            :value="locale"
          >
            <a
              href="#"
              :class="[
                active ? 'bg-gray-100 text-gray-900' : 'text-gray-700',
                'block px-4 py-2 text-sm',
              ]"
              @click="setLocale(locale)"
              >{{ $t('lang.' + locale) }}</a
            >
          </MenuItem>
        </div>
      </MenuItems>
    </transition>
  </Menu>
</template>

<script>
import { Menu, MenuButton, MenuItem, MenuItems } from "@headlessui/vue";
import svgicon from "./svg.vue";
import config from '@/config'
export default {
  components: {
    Menu,
    MenuButton,
    MenuItem,
    MenuItems,
    svgicon
  },
  methods: {
    setLocale(locale) {
      this.$i18n.locale = locale;
    },
  },
  data () {
    return {
      config
    }
  }
};
</script>
