<template>
  <!-- <div class="inline-block">
      <div class="font-mono w-96 bg-green-100 shadow-sm rounded">
          <div class="text-center pt-3">
              <div
                  class="inline-block uppercase mx-2 text-xs text-gray-400"
                  :class="weekdayClass(d)"
                  :key="d"
                  v-for="d in weekdays">{{d}}</div>
          </div>
          <div class="flex justify-center items-end pb-2 px-2">
            <div>&nbsp;</div>
            <div class="font-mono font-normal not-italic text-3xl text-center">{{ctime}}</div>
            <div class="ampm text-xs font-bold">{{am}}</div>
          </div>
      </div>
  </div> -->
  <div class="block font-mono overflow-hidden bg-white text-center w-24">
    <div class="mb-2">
      {{ctime}}<span class="ml-1 text-xs">{{am}}</span>
    </div>
    <div class="bg-green-500 rounded-t-3xl text-white py-1">
      	{{month}}
    </div>
    <div class="pt-1 border-l border-r">
      	<span class="text-4xl font-bold">{{day}}</span>
    </div>
    <div class="pb-3 px-3 border-l border-r border-b rounded-b-3xl border-r flex justify-between">
      	<span class="text-xs font-bold">{{weekname}}</span>
      	<span class="text-xs font-bold">{{year}}</span>
    </div>
  </div>
</template>
<script>
import moment from 'moment'
export default {
    computed: {
      year () {
        return moment().format('YYYY')
      },
      month () {
        return moment().format('MMM')
      },
      day () {
        return moment().format('D')
      },
      weekname () {
        return moment().format('ddd')
      },
      am () {
        return moment().format('A')
      }
    },
    mounted () {
      setInterval(() => {
        this.ctime = this.time()
      }, this.refreshInterval)
    },
    methods: {
      time () {
        return moment().format('hh:mm:ss')
      },
      weekdayClass (p) {
        return moment().format('ddd').toLowerCase() == p.toLowerCase() ? 'text-gray-800 font-bold' : 'text-gray-400'
      }
    },
    data () {
      return {
        ctime: '',
        refreshInterval: 1000,
        weekdays: ["mon", "tue", "wed", "thu", "fri", "sat", "sun"],
      }
    },
}
</script>
